function timeseriesToDataSet(timeseries: TimeSeriesData): DataSet {
  const x: DataSet['x'] = [];
  const y: DataSet['y'] = [];
  const lower: DataSet['lower'] = [];
  const upper: DataSet['upper'] = [];

  // tsa, tsb are the timestamps as strings
